import { useStaticQuery, graphql } from "gatsby"
import { getLangugeTransform } from "utils/i18n"

export const useLayoutFormI18n = language => {
  return getLangugeTransform(useForm(), language)
}

export const useForm = () => {
  const { i18NYaml } = useStaticQuery(graphql`
    query {
      i18NYaml {
        layout {
          form {
            input {
              label {
                es
                en
              }
              placeholder {
                en
                es
              }
              required {
                en
                es
              }
              invalid {
                en
                es
              }
            }
          }
        }
      }
    }
  `)
  return i18NYaml.layout.form
}
