import { useStaticQuery, graphql } from "gatsby"
import { getLangugeTransform } from "utils/i18n"

export const useLayoutFooterI18n = language => {
  const { sections, primary, secondary, tertiary, quaternary } = useFooter()
  return {
    sections: {
      main: getLangugeTransform(sections.main, language),
      primary: getLangugeTransform(sections.primary, language),
      secondary: getLangugeTransform(sections.secondary, language),
      tertiary: getLangugeTransform(sections.tertiary, language),
      quaternary: getLangugeTransform(sections.quaternary, language),
    },
    primary: getLangugeTransform(primary, language),
    secondary: getLangugeTransform(secondary, language),
    tertiary: getLangugeTransform(tertiary, language),
    quaternary: getLangugeTransform(quaternary, language),
  }
}

const useFooter = () => {
  const { i18NYaml } = useStaticQuery(graphql`
    query {
      i18NYaml {
        layout {
          footer {
            sections {
              main {
                title {
                  en
                  es
                }
              }
              primary {
                title {
                  en
                  es
                }
              }
              quaternary {
                title {
                  en
                  es
                }
              }
              secondary {
                title {
                  en
                  es
                }
              }
              tertiary {
                title {
                  en
                  es
                }
              }
            }
            secondary {
              links {
                href {
                  en
                  es
                }
                label {
                  en
                  es
                }
              }
            }
            quaternary {
              links {
                href {
                  en
                  es
                }
                label {
                  en
                  es
                }
              }
            }
            primary {
              links {
                href {
                  en
                  es
                }
                label {
                  en
                  es
                }
              }
            }
            tertiary {
              links {
                href {
                  en
                  es
                }
                label {
                  en
                  es
                }
              }
            }
          }
        }
      }
    }
  `)
  const sections = useFooterSections(i18NYaml),
    { primary, secondary, tertiary, quaternary } = i18NYaml.layout.footer
  return {
    primary,
    secondary,
    tertiary,
    quaternary,
    sections,
  }
}
const useFooterSections = i18NYaml => {
  const {
    main,
    primary,
    secondary,
    tertiary,
    quaternary,
  } = i18NYaml.layout.footer.sections
  return {
    main,
    primary,
    secondary,
    tertiary,
    quaternary,
  }
}
