import React from "react"
import Contact from "vectors/contact.svg"
import { Box, Typography } from "@material-ui/core"
import { useStyles } from "./styles"
import { useLayoutContactI18n } from "hooks/layout/contact"

function Title({ language }) {
  const cl = useStyles()
  const { title, subtitle } = useLayoutContactI18n(language)
  return (
    <Box as="section" className={cl.root}>
      <Contact />
      <Typography variant={"h4"} component="h4" className={cl.title}>
        {title}
      </Typography>
      <Typography
        variant="h6"
        component="h6"
        color="textSecondary"
        className={cl.subtitle}
      >
        {subtitle}
      </Typography>
    </Box>
  )
}

export default Title
