import { useStaticQuery, graphql } from "gatsby"
import { getLangugeTransform } from "utils/i18n"

export const useLayoutHeaderI18n = language => {
  return getLangugeTransform(useHeader(), language)
}

export const useHeader = () => {
  const { i18NYaml } = useStaticQuery(graphql`
    query {
      i18NYaml {
        layout {
          header {
            menus {
              href {
                en
                es
              }
              label {
                en
                es
              }
              isHidden {
                en
                es
              }
            }
            title {
              en
              es
            }
          }
        }
      }
    }
  `)
  return i18NYaml.layout.header
}
