import { useStaticQuery, graphql } from "gatsby"
import { getLangugeTransform } from "utils/i18n"

export const useLayoutContactI18n = language => {
  return getLangugeTransform(useContact(), language)
}

export const useContact = () => {
  const { i18NYaml } = useStaticQuery(graphql`
    query {
      i18NYaml {
        layout {
          contact {
            buttons {
              href {
                en
                es
              }
              label {
                en
                es
              }
            }
            links {
              href {
                es
                en
              }
              label {
                en
                es
              }
            }
            subtitle {
              en
              es
            }
            title {
              en
              es
            }
            button {
              en
              es
            }
            checkbox {
              en
              es
            }
            location {
              en
              es
            }
            phone {
              en
              es
            }
            subsubtitle {
              en
              es
            }
            email {
              en
              es
            }
          }
        }
      }
    }
  `)
  return i18NYaml.layout.contact
}
