import React from "react"
import Img from "gatsby-image"
import { Menu } from "components/layout"
import useImageAbove from "hooks/use-image-above"
import Products from "vectors/products.svg"
import Business from "vectors/business.svg"
import Home from "vectors/home.svg"
import PropTypes from "prop-types"
import { Grid } from "@material-ui/core"
import { useStyles } from "./styles"

function Above({ type, language, children }) {
  const cl = useStyles()
  const fluid = useImageAbove()
  return (
    <Menu language={language} init={type === "init" ? true : false}>
      {type === "init" && (
        <>
          <div className={cl.container_main}>{children}</div>
          <Img
            alt="Image"
            fluid={fluid}
            objectFit="cover"
            className={cl.main}
            objectPosition="50% 50%"
          />
        </>
      )}
      {type === "product" && (
        <Grid container className={cl.container}>
          <Grid item xs={12} sm={6}>
            {children}
          </Grid>
          <Grid item xs={12} sm={6}>
            <Products />
          </Grid>
        </Grid>
      )}
      {type === "business" && (
        <Grid container className={cl.container}>
          <Grid item xs={12} sm={6}>
            {children}
          </Grid>
          <Grid item xs={12} sm={6}>
            <Business />
          </Grid>
        </Grid>
      )}
      {type === "home" && (
        <Grid container className={cl.container}>
          <Grid item xs={12} sm={6}>
            {children}
          </Grid>
          <Grid item xs={12} sm={6}>
            <Home className={cl.image} />
          </Grid>
        </Grid>
      )}
    </Menu>
  )
}

Above.propTypes = {
  type: PropTypes.oneOf(["init", "home", "business", "product"]),
  language: PropTypes.oneOf(["en", "es"]),
}

export default Above
