import React, { useState } from "react"
import { Link } from "gatsby"
import PropTypes from "prop-types"
import { useStyles } from "./styles"
import { useLocation } from "@reach/router"
import GTranslateIcon from "@material-ui/icons/GTranslate"
import MoreVertIcon from "@material-ui/icons/MoreVert"
import { useHeader, useLayoutHeaderI18n } from "hooks/layout/header"
import {
  Box,
  Menu,
  AppBar,
  Button,
  Toolbar,
  MenuItem,
  IconButton,
  Typography,
  useMediaQuery,
} from "@material-ui/core"
import Logo from "vectors/logo.svg"
import LogoInverse from "vectors/logoInverse.svg"

function Header({ children, language, init }) {
  const cl = useStyles()
  const all = useHeader()
  const location = useLocation()
  const [menuHidden, setMenuHidden] = useState(true)
  const [anchorEl, setAnchorEl] = useState(null)
  const isSm = useMediaQuery("(min-width:600px)")
  const { title, menus } = useLayoutHeaderI18n(language)
  const [obj_title] = menus.filter(item => item.label === "init")
  const [obj] = all.menus.filter(
    item => item.href[language] === location.pathname
  )
  const fn = o => (o.es === location.pathname ? o.en : o.es)
  const handleClick = () => setMenuHidden(!menuHidden)
  const handleMenu = event => setAnchorEl(event.currentTarget)
  const handleClose = () => setAnchorEl(null)
  const open = Boolean(anchorEl)
  return (
    <div>
      <Box
        className={
          init
            ? `${cl.pre_header} ${cl.pre_header_init}`
            : `${cl.pre_header} ${cl.pre_header_ninit}`
        }
      >
        <Typography
          variant="overline"
          className={isSm ? cl.textHeader : cl.d_hide}
        >
          +57 3165242979
        </Typography>
        <Typography variant="overline" className={cl.textHeader}>
          comercial1@espaciosmovilessas.com
        </Typography>
      </Box>
      {init && (
        <Box>
          <div className={cl.bg_image}>{children}</div>
        </Box>
      )}
      <AppBar position="static" color="transparent">
        <Toolbar className={cl.bar}>
          <Typography variant="h6" className={cl.title}>
            <Link
              to={obj_title.href}
              className={
                init ? `${cl.a} ${cl.t_light}` : `${cl.a}  ${cl.t_dark}`
              }
            >
              {!init && <Logo />}
              {init && <LogoInverse />}
            </Link>
          </Typography>
          <div className={isSm ? cl.d_block : cl.d_hide}>
            {menus
              .filter(item => !item.isHidden)
              .map((item, i) => (
                <Link key={`${i}-link`} to={item.href} className={cl.a}>
                  <Button className={init ? `${cl.t_light}` : `${cl.t_dark}`}>
                    {item.label}
                  </Button>
                </Link>
              ))}
          </div>
          <Link
            to={fn(obj.href)}
            className={init ? `${cl.a} ${cl.t_light}` : `${cl.a}`}
          >
            <Button className={init ? `${cl.t_light}` : ``}>
              <GTranslateIcon />
            </Button>
          </Link>
          <IconButton
            aria-label="more"
            aria-controls="long-menu"
            aria-haspopup="true"
            className={
              !isSm
                ? init
                  ? `${cl.d_block} ${cl.t_light}`
                  : `${cl.d_block} ${cl.t_dark}`
                : `${cl.d_hide}`
            }
            onClick={handleMenu}
          >
            <MoreVertIcon />
          </IconButton>
          <Menu
            open={!menuHidden}
            onClose={handleClick}
            anchorEl={anchorEl}
            anchorOrigin={{
              vertical: "top",
              horizontal: "right",
            }}
            keepMounted
            transformOrigin={{
              vertical: "top",
              horizontal: "right",
            }}
            open={open}
            onClose={handleClose}
          >
            {menus
              .filter(item => !item.isHidden)
              .map((item, i) => (
                <MenuItem key={`${i}-link-menu`} onClick={handleClose}>
                  <Link to={item.href} className={cl.a}>
                    <Button>{item.label}</Button>
                  </Link>
                </MenuItem>
              ))}
          </Menu>
        </Toolbar>
      </AppBar>
      <Box>{!init && <div className={cl.bg_vector}>{children}</div>}</Box>
    </div>
  )
}

Header.propTypes = {
  init: PropTypes.oneOf([true, false]),
  language: PropTypes.oneOf(["en", "es"]),
}

export default Header
