import React from "react"
import { Link } from "gatsby"
import Logo from "vectors/logo.svg"
import { useStyles } from "./styles"
import { useLayoutFooterI18n } from "hooks/layout/footer"
import {
  Grid,
  useMediaQuery,
  List,
  ListItem,
  Typography,
} from "@material-ui/core"
import { Facebook, LinkedIn, Instagram, WhatsApp } from "@material-ui/icons"

function ListItemLink({ cl, to, children }) {
  return (
    <ListItem button>
      <Link to={to} className={cl.a}>
        {children}
      </Link>
    </ListItem>
  )
}

export default function ({ language }) {
  const cl = useStyles()
  const isSm = useMediaQuery("(min-width:600px)")
  const {
    sections,
    primary,
    secondary,
    tertiary,
    quaternary,
  } = useLayoutFooterI18n(language)
  return (
    <Grid container className={cl.root}>
      <Grid item xs={6} sm={3}>
        <Logo />
        <p>{sections.main.title}</p>
      </Grid>
      <Grid item xs={6} sm={3}>
        <List>
          <ListItem>
            <Typography variant="h6">{sections.primary.title}</Typography>
          </ListItem>
          {primary.links.map((item, i) => (
            <ListItemLink key={`primary_${i}`} to={item.href} cl={cl}>
              {item.label}
            </ListItemLink>
          ))}
        </List>
      </Grid>
      <Grid item xs={6} sm={3}>
        <List>
          <ListItem>
            <Typography variant="h6">{sections.secondary.title}</Typography>
          </ListItem>
          {secondary.links.map((item, i) => (
            <ListItemLink key={`secondary_${i}`} to={item.href} cl={cl}>
              {item.label}
            </ListItemLink>
          ))}
        </List>
      </Grid>
      <Grid item xs={6} sm={3}>
        <List>
          <ListItem>
            <Typography variant="h6">{sections.tertiary.title}</Typography>
          </ListItem>
          {tertiary.links.map((item, i) => (
            <ListItemLink key={`tertiary_${i}`} to={item.href} cl={cl}>
              {item.label}
            </ListItemLink>
          ))}
          <ListItem>
            <Typography variant="h6">{sections.quaternary.title}</Typography>
          </ListItem>
          {quaternary.links.map((item, i) => {
            const { label, href } = item
            if (item.label === "Facebook") {
              return (
                <a key={`quaternary_${i}`} href={item.href} target="_blank">
                  <Facebook fontSize="large" color="primary" />
                </a>
              )
            }
            if (item.label === "Instagram") {
              return (
                <a key={`quaternary_${i}`} href={item.href} target="_blank">
                  <Instagram fontSize="large" color="primary" />
                </a>
              )
            }
            if (item.label === "Linkedin") {
              return (
                <a key={`quaternary_${i}`} href={item.href} target="_blank">
                  <LinkedIn fontSize="large" color="primary" />
                </a>
              )
            }
            if (item.label === "Whatsapp") {
              return (
                <a key={`quaternary_${i}`} href={item.href} target="_blank">
                  <WhatsApp fontSize="large" color="secondary" />
                </a>
              )
            }
          })}
        </List>
      </Grid>
    </Grid>
  )
}
