import React, { useState } from "react"
import { useStyles } from "./styles"
import { Formik } from "formik"
import * as Yup from "yup"
import { post_contact } from "queries/contact"
import { useLayoutContactI18n } from "hooks/layout/contact"
import { useLayoutFormI18n } from "hooks/layout/forms"
import {
  Box,
  Grid,
  Paper,
  Button,
  Checkbox,
  InputLabel,
  Typography,
  FormControl,
  OutlinedInput,
  FormHelperText,
  FormControlLabel,
} from "@material-ui/core"
import {
  Phone,
  Email,
  Comment,
  Facebook,
  LinkedIn,
  WhatsApp,
  Instagram,
  LocationOn,
  AccountBox,
  MailOutline,
  PhoneEnabled,
} from "@material-ui/icons"

const validation = (formik, str) =>
  Boolean(formik.touched[str] && formik.errors[str])

const touchaction = (formik, str) => Boolean(formik.values[str])

const ErrorMsg = ({ msg }) => <FormHelperText error>{msg}</FormHelperText>

export default function ({ language }) {
  const cl = useStyles(),
    [checked, setChecked] = useState(false),
    {
      subsubtitle,
      button,
      checkbox,
      phone,
      location,
      email,
      links,
    } = useLayoutContactI18n(language),
    { input } = useLayoutFormI18n(language),
    handleChecked = () => setChecked(!checked)
  return (
    <Box className={cl.root}>
      <Paper elevation={3} className={cl.card}>
        <Grid container>
          <Grid item xs={12} sm={6}>
            <Typography variant="h5" component="h5" paragraph={true}>
              {subsubtitle}
            </Typography>
            <Typography
              variant="subtitle2"
              color="textSecondary"
              paragraph={true}
            >
              <Phone color="primary" /> {phone}
            </Typography>
            <Typography
              variant="subtitle2"
              color="textSecondary"
              paragraph={true}
            >
              <LocationOn color="primary" /> {location}
            </Typography>
            <Typography
              variant="subtitle2"
              color="textSecondary"
              paragraph={true}
            >
              <Email color="primary" /> {email}
            </Typography>
            {links.map((item, i) => {
              const { label, href } = item
              console.log(label, href)
              switch (label) {
                case "Facebook":
                  return (
                    <a key={`contact_${i}`} href={item.href} target="_blank">
                      <Facebook fontSize="large" color="primary" />
                    </a>
                  )
                  break
                case "Instagram":
                  return (
                    <a key={`contact_${i}`} href={item.href} target="_blank">
                      <Instagram fontSize="large" color="primary" />
                    </a>
                  )
                  break
                case "Linkedin":
                  return (
                    <a key={`contact_${i}`} href={item.href} target="_blank">
                      <LinkedIn fontSize="large" color="primary" />
                    </a>
                  )
                  break
                default:
                  return (
                    <div key={`contact_${i}`} className={cl.my}>
                      <a href={item.href} target="_blank" className={cl.a}>
                        <Button variant="contained" color="secondary">
                          <WhatsApp color="inherit" />
                          {label}
                        </Button>
                      </a>
                    </div>
                  )
              }
            })}
          </Grid>
          <Grid item xs={12} sm={6}>
            <Formik
              initialValues={{ name: "", email: "", phone: "", message: "" }}
              validationSchema={Yup.object({
                name: Yup.string()
                  .min(3, input[0].invalid)
                  .required(input[0].required),
                email: Yup.string()
                  .email(input[1].invalid)
                  .required(input[1].required),
                phone: Yup.number()
                  .min(10, input[2].invalid)
                  .required(input[2].required),
                message: Yup.string()
                  .min(15, input[3].invalid)
                  .required(input[3].required),
              })}
              onSubmit={values => post_contact(values)}
            >
              {formik => {
                const isValidName = validation(formik, "name"),
                  isTouchedName = touchaction(formik, "name"),
                  isValidEmail = validation(formik, "email"),
                  isTouchedEmail = touchaction(formik, "email"),
                  isValidPhone = validation(formik, "phone"),
                  isTouchedPhone = touchaction(formik, "phone"),
                  isValidMessage = validation(formik, "message"),
                  isTouchedMessage = touchaction(formik, "message"),
                  iconColor = (valid, touched) =>
                    valid ? "error" : touched ? "primary" : "inherit"
                return (
                  <form onSubmit={formik.handleSubmit}>
                    <FormControl
                      fullWidth
                      variant="outlined"
                      className={cl.form}
                    >
                      <InputLabel error={isValidName} htmlFor="name">
                        {input[0].label}
                      </InputLabel>
                      <OutlinedInput
                        id="name"
                        name="name"
                        labelWidth={70}
                        error={isValidName}
                        placeholder={input[0].placeholder}
                        endAdornment={
                          <AccountBox
                            color={iconColor(isValidName, isTouchedName)}
                          />
                        }
                        {...formik.getFieldProps("name")}
                      />
                      {isValidName && <ErrorMsg msg={formik.errors.name} />}
                    </FormControl>

                    <FormControl
                      fullWidth
                      variant="outlined"
                      className={cl.form}
                    >
                      <InputLabel error={isValidEmail}>
                        {input[1].label}
                      </InputLabel>
                      <OutlinedInput
                        name="email"
                        labelWidth={135}
                        error={isValidEmail}
                        placeholder={input[1].placeholder}
                        endAdornment={
                          <MailOutline
                            color={iconColor(isValidEmail, isTouchedEmail)}
                          />
                        }
                        {...formik.getFieldProps("email")}
                      />
                      {isValidEmail && <ErrorMsg msg={formik.errors.email} />}
                    </FormControl>

                    <FormControl
                      fullWidth
                      variant="outlined"
                      className={cl.form}
                    >
                      <InputLabel error={isValidPhone} htmlFor="phone">
                        {input[2].label}
                      </InputLabel>
                      <OutlinedInput
                        id="phone"
                        name="phone"
                        labelWidth={135}
                        error={isValidPhone}
                        placeholder={input[2].placeholder}
                        endAdornment={
                          <PhoneEnabled
                            color={iconColor(isValidPhone, isTouchedPhone)}
                          />
                        }
                        {...formik.getFieldProps("phone")}
                      />
                      {isValidPhone && <ErrorMsg msg={formik.errors.phone} />}
                    </FormControl>

                    <FormControl
                      fullWidth
                      variant="outlined"
                      className={cl.form}
                    >
                      <InputLabel error={isValidMessage} htmlFor="message">
                        {input[3].label}
                      </InputLabel>
                      <OutlinedInput
                        id="message"
                        name="message"
                        labelWidth={170}
                        error={isValidMessage}
                        placeholder={input[3].placeholder}
                        endAdornment={
                          <Comment
                            color={iconColor(isValidMessage, isTouchedMessage)}
                          />
                        }
                        {...formik.getFieldProps("message")}
                      />
                      {isValidMessage && (
                        <ErrorMsg msg={formik.errors.message} />
                      )}
                    </FormControl>

                    <div>
                      <FormControlLabel
                        control={
                          <Checkbox
                            checked={checked}
                            onChange={handleChecked}
                            inputProps={{ "aria-label": "primary checkbox" }}
                          />
                        }
                        className={cl.form}
                        label={checkbox}
                      />
                    </div>

                    <Button
                      type="submit"
                      color="primary"
                      variant="contained"
                      disabled={!formik.isValid}
                    >
                      {button}
                    </Button>
                  </form>
                )
              }}
            </Formik>
          </Grid>
        </Grid>
      </Paper>
    </Box>
  )
}
